/* Estructura Principal */

.tarea-formulario {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;     
}

/* Campo de Texto */

.tarea-input {
  width: 350px;
  font-size: 18px;
  background-color: white;
  padding: 14px 32px 14px 16px;
  border-radius: 4px 0 0 4px;
  border: 2px solid #0e4781;
  outline: none;
}

/* Boton Agregar Tarea */

.tarea-boton {
  padding: 16px;
  font-size: 18px;
  font-family: Roboto, sans-serif;
  border: none;
  border-radius: 0 4px 4px 0;
  cursor: pointer;
  outline: none;
  background: #0e4781;
  color: #fff;
  text-transform: capitalize;
}

@media screen and (max-width: 768px) {
    .tarea-boton {
      padding: 7px;
      margin-top: 5px;
      font-size: 10px;
    }
}