.tarea-contenedor {
  width: 500px;
  min-height: 65px;
  background-color: #1b1b32;
  margin: 5px 0;
  padding: 8px 15px 8px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 7px;
  border: 1px solid white;
  cursor: pointer;    
}

/* Colores */

.tarea-contenedor:nth-child(3n + 1) {
  background-color: #1d5288;
}

.tarea-contenedor:nth-child(3n + 2) {
  background-color: #2b5b8a;
}

.tarea-contenedor:nth-child(3n + 3) {
  background-color: #3f668d;
}

/* Texto */

.tarea-texto {
  width: 100%;
  height: 100%;
  font-size: 20px;
  display: flex;
  align-items: center;
  overflow-wrap: anywhere;
}

/* Icono */

.tarea-icono {
  width: 25px;
  height: 25px;
  margin: 5px;
}

/* Tarea Completada */

.tarea-contenedor.completada {
  background-color: #5a01a7;
  text-decoration: line-through;
}

@media screen and (max-width: 768px) {
  .tarea-texto {
    font-size: 15px;
  }
}